@use "../var"as *;

.footer {
  background-color: $bg-footer-color;
}

.footer__container {
  display: flex;
  justify-content: space-between;
  padding: 60px 0;
}

.footer__form {
  max-width: 536px;
  width: 100%;
}

.footer__form-title {
  font-size: 16px;
  line-height: calc(23 / $p);
  padding-bottom: 14px;
}

.footer__form-text {
  // max-width: 476px;
  // width: 100%;
  padding-bottom: 16px;
  text-align: left;
  word-spacing: 0;
}

.footer__form-link {
  font-weight: 600;
  color: $title-color;
  font-size: 14px;
  line-height: calc(28 / 14);
  padding-bottom: 16px;
  transition: all 0.3s ease-in-out;
  &:hover {
    color: #000;
  }
}
.footer__form-send {
  display: flex;
  justify-content: space-between;
}

.form__label {
}

.form__input {
  max-width: 368px;
  width: 100%;
  padding-left: 18px;
  padding-top: 6px;
  padding-bottom: 6px;
}

.form__btn {
  max-width: 160px;
  width: 100%;
  padding: 6px 12px;
  align-items: center;
}

.list__item {
  transition: all 0.3s ease-in-out;
  padding-bottom: 10px;
  &:hover {
    transform: scale(1.2);
  }
}

.list__item-link {
  font-weight: 600;
  font-size: 14px;
  line-height: calc(28 / 14);
  color: rgb(112, 112, 112);
}

.footer__copy {
  background-color: $bg-footer-copy-color;
  // margin: 0 auto;
}
.footer__copy-container {
  padding: 40px 0;
  text-align: center;
}

.footer__copy-link {
  text-align: center;
  color: #a1a1a1;
}
