@charset "UTF-8";
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,regular,500,600,700,800,300italic,italic,500italic,600italic,700italic,800italic&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:100,200,300,regular,500,600,700,800,900,100italic,200italic,300italic,italic,500italic,600italic,700italic,800italic,900italic&display=swap);
html {
  box-sizing: border-box;
}

*,
::after,
::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  border: 0;
}

ol[class],
ul[class] {
  list-style: none;
}

a {
  text-decoration: none;
}

button,
a {
  cursor: pointer;
}

img {
  max-width: 100%;
  display: block;
}

button,
input,
select,
textarea {
  color: inherit;
  border: 0;
}

input,
textarea {
  border: none;
  border-radius: 0;
  outline: none;
}

html, body {
  height: 100%;
  margin: 0;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1; /* Основное содержимое займет все доступное пространство */
}

body {
  background-color: #fff;
  font-family: "Open Sans", "Helvetica", "Arial", sans-serif;
  font-size: calc(14px + 2 * (100vw - 340px) / 793);
  font-weight: 400;
  line-height: 1.6875;
  letter-spacing: 0;
  color: #5a5a5a;
}

.container {
  max-width: 1133px;
  width: 100%;
  margin: 0 auto;
  padding: 0 2px;
}

.input {
  background-color: transparent;
  border: 1px solid #383636;
  border-radius: 21px;
  line-height: 1.75;
  align-items: left;
  color: #a1a1a1;
  transition: all 0.3s ease-in-out;
}
.input::placeholder {
  color: #a1a1a1;
}
.input::placeholder:hover {
  color: #000;
}
.input:hover {
  color: #363838;
}
.input:focus {
  border-color: #363838;
  color: #363838;
}

.btn {
  background-color: #363838;
  color: #fff;
  font-size: 16;
  border-radius: 21px;
  font-weight: 600;
  line-height: 1.75;
  align-items: center;
  border: 1px #363838 solid;
  transition: background-color, color, 0.3s ease-out;
  user-select: none;
}
.btn:hover {
  background-color: transparent;
  color: #363838;
}
.btn:active {
  transform: scale(0.96);
}

.logo {
  width: 98px;
  height: 33px;
  border-radius: 5px;
}
.logo__img {
  transition: all 0.3s ease-out;
}
.logo__img:hover {
  transform: scale(1.1);
}

.link {
  transition: all 0.3s ease-in-out;
}
.link:hover {
  color: #000;
  transform: scale(1.2);
}

.link--active {
  position: relative;
  font-family: 110;
  font-weight: 700;
  font-size: 16px;
  color: rgba(54, 56, 56, 0.9019607843);
  transition: all 0.3s ease-in-out;
}
.link--active:hover {
  color: #000;
  transform: scale(1.2);
}
.link--active:hover::after {
  background-color: #000;
}
.link--active::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  bottom: 0;
  left: 0;
  border-radius: 5px;
  background-color: #363838;
  visibility: visible;
}

.header__nav-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 40px;
  padding-bottom: 40px;
}

.header__nav-link {
  font-family: 110;
  font-weight: 700;
  font-size: 16px;
  color: rgba(54, 56, 56, 0.9019607843);
}

.footer {
  background-color: #dfdfdf;
}

.footer__container {
  display: flex;
  justify-content: space-between;
  padding: 60px 0;
}

.footer__form {
  max-width: 536px;
  width: 100%;
}

.footer__form-title {
  font-size: 16px;
  line-height: 1.4375;
  padding-bottom: 14px;
}

.footer__form-text {
  padding-bottom: 16px;
  text-align: left;
  word-spacing: 0;
}

.footer__form-link {
  font-weight: 600;
  color: #363838;
  font-size: 14px;
  line-height: 2;
  padding-bottom: 16px;
  transition: all 0.3s ease-in-out;
}
.footer__form-link:hover {
  color: #000;
}

.footer__form-send {
  display: flex;
  justify-content: space-between;
}

.form__input {
  max-width: 368px;
  width: 100%;
  padding-left: 18px;
  padding-top: 6px;
  padding-bottom: 6px;
}

.form__btn {
  max-width: 160px;
  width: 100%;
  padding: 6px 12px;
  align-items: center;
}

.list__item {
  transition: all 0.3s ease-in-out;
  padding-bottom: 10px;
}
.list__item:hover {
  transform: scale(1.2);
}

.list__item-link {
  font-weight: 600;
  font-size: 14px;
  line-height: 2;
  color: rgb(112, 112, 112);
}

.footer__copy {
  background-color: #363838;
}

.footer__copy-container {
  padding: 40px 0;
  text-align: center;
}

.footer__copy-link {
  text-align: center;
  color: #a1a1a1;
}