@use 'var' as *;
@use 'base/font-mixin' as *;
@use 'base/press-footer' as *;

body {
  background-color: $bg-color;
  @include safe-font('Open Sans', $font-type);
  @include font-media($p, $p-mob);
  font-weight: 400;
  line-height: calc(27 / $p);
  letter-spacing: 0;
  color: $text-color;
}

.container {
  max-width: #{$maxWidth}px;
  width: 100%;
  margin: 0 auto;
  padding: 0 2px;
}
